<template>
  <view class="container" :style="{width:widthx +'px',height:heightx +'px'}">
    <view class="sign-box" :style="{width:widthx +'px',height:heightx +'px'}">
      <canvas class="mycanvas"
              :style="{width:widthx +'px',height:heightx +'px',lineHeight: heightx +'px'}"
              canvas-id="mycanvas"
              @touchstart="touchstart"
              @touchmove="touchmove"
              @touchend="touchend"
			  >
			  </canvas>
		<view class="shuom"  v-if="shumin" :style="!rotate?'transform: rotate(90deg);':''">{{explain}}</view>
       <canvas canvas-id="camCacnvs"
              :style="{width:heightx +'px',height:widthx +'px'}"
               class="canvsborder"></canvas>
 
    </view>
  </view>
 
</template>
<script>
var x = 20;
var y = 20;
var tempPoint = []; //用来存放当前画纸上的轨迹点
let that;
let canvasw;
let canvash;
export default {
  props: {
	  // 控制大小
      height: {
		type: Number,
        default: 0
      },
      width: {
		type: Number,
        default: 0
      },
	  // 控制画笔画板
      strokeStyle: {
        default: '#000',
        type: String
      },
	  lineWidth: {
	  		type: Number,
	    default: 3
	  },
	  // 提示说明
      explain:{
        default: '请在此签名',
        type: String
      },
	  // 控制生成图片是否横屏
      rotate:{//默认横屏
        type: [Boolean, String],
        default: false,
      },
   },
  data() {
    return {
      ctx: '', //绘图图像
      points: [], //路径点集合,
      widthx: 0,
      heightx: 0,
	  shumin:true
    };
  },
  mounted() {
	  that = this;
	  this.ctx = uni.createCanvasContext('mycanvas', this); //创建绘图对象
	  //设置画笔样式
	  this.ctx.lineWidth = 4;
	  this.ctx.lineCap = 'round';
	  this.ctx.lineJoin = 'round';
	  this.ctx.setStrokeStyle(this.strokeStyle)
      this.ctx.setLineWidth(this.lineWidth)
	  uni.getSystemInfo({
	    success: function (res) {
	      console.log(res);
		  if(that.height){
			that.heightx = that.height;
		  }else{
			that.heightx = res.screenHeight;     
		  }
		  if(that.width){
		  	that.widthx = that.width;
		  }else{
		  	that.widthx = res.windowWidth;   
		  }
	    }
	  });
  },
  methods: {
    //触摸开始，获取到起点
    touchstart: function (e) {
      let startX = e.changedTouches[0].x;
      let startY = e.changedTouches[0].y;
      let startPoint = {
        X: startX,
        Y: startY
      };
 
      /* **************************************************
				    #由于uni对canvas的实现有所不同，这里需要把起点存起来
				 * **************************************************/
      this.points.push(startPoint);
 
      //每次触摸开始，开启新的路径
      this.ctx.beginPath();
    },
 
    //触摸移动，获取到路径点
    touchmove: function (e) {
      let moveX = e.changedTouches[0].x;
      let moveY = e.changedTouches[0].y;
      let movePoint = {
        X: moveX,
        Y: moveY
      };
      this.points.push(movePoint); //存点
      let len = this.points.length;
      if (len >= 2) {
        this.draw(); //绘制路径
      }
      tempPoint.push(movePoint);
    },
 
    // 触摸结束，将未绘制的点清空防止对后续路径产生干扰
    touchend: function () {
      this.points = [];
    },
 
    /* ***********************************************	
			#   绘制笔迹
			#   1.为保证笔迹实时显示，必须在移动的同时绘制笔迹
			#   2.为保证笔迹连续，每次从路径集合中区两个点作为起点（moveTo）和终点(lineTo)
			#   3.将上一次的终点作为下一次绘制的起点（即清除第一个点）
			************************************************ */
    draw: function () {
		this.shumin = false
      let point1 = this.points[0];
      let point2 = this.points[1];
      this.points.shift();
      this.ctx.moveTo(point1.X, point1.Y);
      this.ctx.lineTo(point2.X, point2.Y);
      this.ctx.stroke();
      this.ctx.draw(true);
    },
 
    //清空画布
    handleReset: function () {
      console.log('handleReset');
      that.ctx.clearRect(0, 0, that.widthx, that.heightx);
      that.ctx.draw(true);
      tempPoint = [];
	  this.shumin = true
    },
 
    //获取签名
    handleConfirm() {
		return new Promise((resolve, reject) => {
		  if (tempPoint.length == 0) {
			reject({
				message:'请先签名',
				type: 0
			})
			return;
		  }
		  let that = this
		  uni.canvasToTempFilePath({
			canvasId: 'mycanvas',
			success: function (res) {
				// console.log(res.tempFilePath)
			  let tempPath = res.tempFilePath;
			  if(!that.rotate){
				  const ctx = uni.createCanvasContext('camCacnvs', that);
				  ctx.translate(0, that.widthx);
				  ctx.rotate((-90 * Math.PI) / 180);  
				  ctx.drawImage(tempPath, 0, 0, that.widthx, that.heightx);
				  ctx.draw();
				  setTimeout(() => {
					uni.canvasToTempFilePath(
					  {
						canvasId: 'camCacnvs',
						success: function (res) {
							//这是签名图片文件的本地临时地址
						    let path = res.tempFilePath;
							resolve(path)
						},fail: err => {
						  reject(err)
						}
					  },
					  this
					);
				  }, 200);
			  }else{
				resolve(tempPath) 
			  }
			}
		  });
	  })
    }
  }
};
</script>
 
<style lang="scss" scoped>
	.shuom{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		bottom: 0;
		color: #ccc;
		z-index: 0;
	}
.container {
  display: flex;
  flex-direction: row;
}
 
.sign-box {
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}
 
.sign-view {
  height: 100%;
}
 
.sigh-btns {
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
 
.btn {
  margin: auto;
  padding: 8rpx;
  transform: rotate(90deg);
  border: grey 1rpx solid;
}
 
.mycanvas {
  margin: auto 0rpx;
  text-align: center;
  z-index: 1;
  color: #ccc;
}
 
.canvsborder {
  border: 1rpx solid #333;
  position: fixed;
  top: 0;
  left: 10000rpx;
}
</style>